<template>
  <div class="section">
    <div class="tabs">
      <div :class="{active:tab == 1}" @click="tab = 1">固井工具</div>
      <div :class="{active:tab == 2}" @click="tab = 2">固井作业</div>
    </div>
    <table v-show="tab==1" >
      <tr>
        <td rowspan="3" >磁性定位下入井深</td>
        <td colspan="2" >1#(m)</td>
        <td width=40%></td>
      </tr>
      <tr>
        <td colspan="2">2#(m)</td>
        <td ></td>
      </tr>
      <tr>
        <td colspan="2">3#(m)</td>
        <td ></td>
      </tr>
      <tr>
        <td rowspan="2">分级箍</td>
        <td colspan="2">型号、规格</td>
        <td ></td>
      </tr>
      <tr>
        <td colspan="2">下入井深(m)</td>
        <td ></td>
      </tr>
      <tr>
        <td rowspan="4">尾(筛)管外封隔器</td>
        <td rowspan="2" >上部</td>
        <td >型号、规格</td>
        <td ></td>
      </tr>
      <tr>
        <td >下入井深(m)</td>
        <td ></td>
      </tr>
      <tr>
        <td rowspan="2">下部</td>
        <td >型号、规格</td>
        <td ></td>
      </tr>
      <tr>
        <td >下入井深(m)</td>
        <td ></td>
      </tr>
      <tr>
        <td >尾(筛)管</td>
        <td  colspan="2">型号、规格</td>
        <td ></td>
      </tr>
      <tr>
        <td >悬挂器</td>
        <td  colspan="2">下入井深(m)</td>
        <td ></td>
      </tr>
      <tr>
        <td  colspan="3">尾管与上层套管重叠段长度(m)</td>
        <td ></td>
      </tr>
      <tr>
        <td  colspan="3">尾(筛)管串喇叭口顶部井深(m)</td>
        <td ></td>
      </tr>
    </table>
    <table v-show="tab==2" class="sheet">
      <tr>
        <td  colspan="2">套管名称</td>
        <td  colspan="3">表层套管</td>
        <td  colspan="3">技术套管</td>
        <td  colspan="3">生产套管</td>
      </tr>
      <tr>
        <td  colspan="2">施工日期年月日</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.operate_at}}</td>
      </tr>
      <tr>
        <td  colspan="2">固井方式</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.cement_method}}</td>
      </tr>
      <tr>
        <td  colspan="2">套管外径(mm)</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.case_outer_diameter}}</td>
      </tr>
      <tr>
        <td  colspan="2">井眼直径(mm)</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.wellbore_diameter}}</td>
      </tr>
      <tr>
        <td  colspan="2">井段(m)</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.section}}</td>
      </tr>
      <tr>
        <td  colspan="2">固井质量评价</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.evaluation}}</td>
      </tr>
      <tr>
        <td  colspan="2">套(尾、筛)管鞋井深(m)</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.shoe_depth}}</td>
      </tr>
      <tr>
        <td  colspan="2">套(尾、筛)管浮箍井深(m)</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.floatcollar_depth}}</td>
      </tr>
      <tr>
        <td  colspan="2">套管旋流器井深(m)</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.case_cyclone_depth}}</td>
      </tr>
      <tr>
        <td  colspan="2">人工井底井深(m)</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.artificial_bottom_depth}}</td>
      </tr>
      <tr>
        <td  colspan="2">油(气)层顶部井深(m)</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.reservoir_top_depth}}</td>
      </tr>
      <tr>
        <td  colspan="2">油(气）层底部井深(m)</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.reservoir_bottom_depth}}</td>
      </tr>
      <tr>
        <td  colspan="2">钻井液体系类别</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.fluid_system}}</td>
      </tr>
      <tr>
        <td  colspan="2">注水泥前钻井液密度(g/cm³)</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.fluid_density_before}}</td>
      </tr>
      <tr>
        <td  rowspan="3" style="width: 120px;">隔离液(前置液)</td>
        <td >名称</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.padfluid_name}}</td>
      </tr>
      <tr>
        <td >替入量(m³)</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.padfluid_substitute_volume}}</td>
      </tr>
      <tr>
        <td >药品量(kg)</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.padfluid_drug_volume}}</td>
      </tr>
      <tr>
        <td  colspan="2">水泥品种及标号</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.cement_type_number}}</td>
      </tr>
      <tr>
        <td  colspan="2">水泥浆体系类型</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.cement_fluid_system}}</td>
      </tr>
      <tr>
        <td  rowspan="2">水泥浆密度(g/cm³)</td>
        <td >设计</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.design_cement_fluid_density}}</td>
      </tr>
      <tr>
        <td >实际</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.actual_cement_fluid_density}}</td>
      </tr>
      <tr>
        <td  rowspan="2">替入量(m³)</td>
        <td >设计</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.design_substitute_volume}}</td>
      </tr>
      <tr>
        <td >实际</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.actual_substitute_volume}}</td>
      </tr>
      <tr>
        <td  rowspan="2">水泥用量(t)</td>
        <td >设计</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.design_cement_volume}}</td>
      </tr>
      <tr>
        <td >实际</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.actual_cement_volume}}</td>
      </tr>
      <tr>
        <td  colspan="2">上返速度(m/s)</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.up_speed}}</td>
      </tr>
      <tr>
        <td  rowspan="2">试压结果(MPa)</td>
        <td >压力</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.pressure_test_pressure}}</td>
      </tr>
      <tr>
        <td >30min压降</td>
        <td colspan="3" v-for="(item,i) in list_operation" :key="i">{{item.pressure_test30min}}</td>
      </tr>
      <tr>
        <td  colspan="2" :rowspan="rowspan">水泥外加剂名称及用量</td>
        <td  v-for="(item,i) in td_title" :key="i">{{item}}</td>
      </tr>
      <!-- <template v-for="item in total">
        <tr v-for="(cur,index) in item">
          <template>
            <td>{{cur.additive_type}}</td>
            <td>{{cur.additive_name}}</td>
            <td>{{cur.additive_usage}}</td>
          </template>
        </tr>
      </template> -->

      <!-- <template v-for="item in total">
        <tr v-for="(cur,m) in item">
          
          <td>{{cur}}</td>
        </tr>
      </template> -->
    </table>
  </div>
</template>
<script>
  import Lay from '@/components/common/template';
  import request from "@/utils/request";
  export default {
    props: ["route"],
    data() {
      return {
        current: 1,
        list_tool: [],
        list_operation: [],
        total: [], //水泥外加剂,
        rowspan: 0, //表层套管单元格跨列数
        colspan: 0, //水泥外加剂名称及用量单元格跨行数
        tab: 1,
        td_title: [] //材料类别	材料名称	材料用量
      }
    },
    computed: {},
    mounted: function () {
      let data = this.route;
      // 请求固井工具数据
      request({
        url: "/data/query/cementtooldata",
        method: "post",
        data
      }).then(res => {
        console.log(res);
        this.list_tool.push(res);
      });
      
      // 请求固井作业数据
      request({
        url: "/data/query/cementoperationdata",
        method: "post",
        data
      }).then(res => {
        // console.log(res);
        this.list_operation = res;
        this.colspan = res.length * 3; //动态跨列数
        this.rowspan = res[0].cement_additive_data_list.length + 1; //动态跨行数
        this.createTitle(res.length); //动态字段数量
        
      });
    },
    methods: {
      // 生成动态字段数量
      createTitle(leng) {
        for (let i = 0; i < leng; i++) {
          this.td_title.push("材料类别", "材料名称", "材料用量");
        }
      }
    }
  }

</script>



